import React from 'react'
import "./pageinitiation.css";

function Pageinitiation() {
  return (
    <>
      {/* pageintiation */}
      <div className="mb-4 my-4 text-dark">
        <nav aria-label="...">
          <ul className="pagination justify-content-center">
            <li className="page-item disabled">
              <span
                className="page-link"
                style={{ color: "#000", background: "#fff" }}
              >
                Previous
              </span>
            </li>
            <li className="page-item active">
              <a
                className="page-link"
                href="#"
                style={{ color: "#fff", background: "#000", border: "1px solid #000" }}
              >
                1
              </a>
            </li>
            <li className="page-item" aria-current="page">
              <span
                className="page-link"
                style={{ color: "#000", background: "#fff" }}
              >
                2
              </span>
            </li>
            <li className="page-item">
              <a
                className="page-link"
                href="#"
                style={{ color: "#000", background: "#fff" }}
              >
                3
              </a>
            </li>
            <li className="page-item">
              <a
                className="page-link"
                href="#"
                style={{ color: "#000", background: "#fff" }}
              >
                Next
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </>
  );
}

export default Pageinitiation