import React from "react";

function GarCol() {
  return (
    <>
      <img
        src={process.env.PUBLIC_URL + "/assets/Garcol.jpg"}
        alt="Gaurantee Col?"
        style={{
          height: "80%",
          width: "80%",
          marginTop: "1.6rem",
          marginBottom: "1.4rem",
          marginLeft: "9.5rem",
          marginRight: "1rem",
        }}
      />
    </>
  );
}

export default GarCol;
